/*
  Josh's Custom CSS Reset
  https://www.joshwcomeau.com/css/custom-css-reset/
*/

*,
*::before,
*::after {
  box-sizing: border-box;
}

* {
  margin: 0;
}

@media (prefers-reduced-motion: no-preference) {
  html {
    interpolate-size: allow-keywords;
  }
}

body {
  line-height: 1.5;
  -webkit-font-smoothing: antialiased;
}

img,
picture,
video,
canvas,
svg {
  display: block;
  max-width: 100%;
}

input,
button,
textarea,
select {
  font: inherit;
}

p,
h1,
h2,
h3,
h4,
h5,
h6 {
  overflow-wrap: break-word;
}

p {
  text-wrap: pretty;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  text-wrap: balance;
}

#root,
#__next {
  isolation: isolate;
}

/* Dropdown Menu */

:root {
  --gray-100: #f5f5f5;
  --gray-900: #212121;
  --white: #fff;
}

body {
  font-family:
    Inclusive Sans,
    Arial,
    Helvetica,
    sans-serif;
  color: var(--gray-900);
}

nav {
  padding: 40px;
}

.primary-list,
.secondary-list {
  list-style: none;
  padding-left: 0;
}

.primary-item,
.secondary-item {
  cursor: pointer;
  padding: 5px 10px;
  border-radius: 5px;
}

.primary-item:hover,
.secondary-item:hover {
  background-color: var(--gray-100);
}

.primary-list {
  display: flex;
  justify-content: center;
  gap: 40px;
}

.has-secondary-list {
  position: relative;
  display: flex;
  align-items: center;
}

.has-secondary-list::after {
  content: "";
  display: block;
  width: 15px;
  height: 15px;
  margin-left: 5px;
  background-position: center;
  background-repeat: no-repeat;
  background-size: 15px;
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_4___});
}

.has-secondary-list:hover::after {
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_5___});
}

.secondary-list {
  position: absolute;
  top: 100%;
  left: 0;
  width: 100%;
  padding-top: 10px;
  display: none;
}

.shown {
  display: block;
}
